import { SAVE_USER } from 'src/app/graphQL/queries/mutations/local/save-user';
import { Component, OnInit } from '@angular/core';
import { Apollo } from 'apollo-angular';
import { Router } from '@angular/router';
import { SINGIN } from 'src/app/graphQL/queries/mutations/signin';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ApolloError } from 'apollo-client';

@Component({
  selector: 'app-signin-pages',
  templateUrl: './signin-pages.component.html',
  styleUrls: ['./signin-pages.component.scss']
})
export class SigninPagesComponent implements OnInit {
  signInForm: FormGroup;
  hasError = {
    email: false,
    password: false
  };
  errorMessage = '';
  hasWarning = false;

  constructor(private apollo: Apollo, private router: Router) {
    this.signInForm = new FormGroup({
      email: new FormControl('', [Validators.required]),
      password: new FormControl('', [Validators.required, Validators.minLength(8)])
    });
  }

  ngOnInit() {}

  private saveUser(user) {
    this.apollo.mutate({ mutation: SAVE_USER, variables: { user } }).subscribe({
      next: ({ data }) => {
        console.log(data);
      },
      error: e => {
        console.log('ERROR!');
        console.log(e);
      }
    });
  }

  onSubmit() {
    this.apollo.mutate({ mutation: SINGIN, variables: { ...this.signInForm.value } }).subscribe({
      next: ({ data }) => {
        const { user, token } = data.authResponse;
        localStorage.setItem('token', token);
        this.saveUser(user);
        this.router.navigate(['/dashboard']);
      },
      error: (error: ApolloError) => {
        const { password, email } = error.graphQLErrors[0].extensions.exception;
        this.hasError.email = email;
        this.hasError.password = password;
        this.errorMessage = error.graphQLErrors[0].message;
      }
    });
  }
}
